// --- import css ---
import 'font-awesome/css/font-awesome.min.css'
import '@/assets/bootstrap/css/bootstrap.min.css'
import '@/assets/ace_admin-1.4.0/css/ace-fonts.css'
import '@/assets/ace_admin-1.4.0/css/ace.css'
import '@/assets/ace_admin-1.4.0/css/ace-skins.css'

// --- import js ---
import 'jquery'
import 'jquery-slimscroll'
import 'jquery-ui/ui/widgets/sortable.js'
import 'jquery-ui/ui/widgets/datepicker.js'
import 'jquery-ui/themes/base/datepicker.css'
import '@/assets/bootstrap/js/bootstrap.js'
import '@/assets/jqueryui-touch-punch/jquery.ui.touch-punch.js'
import '@/assets/ace_admin-1.4.0/js/ace-extra.js'
import '@/assets/ace_admin-1.4.0/js/ace-elements.js'
import '@/assets/ace_admin-1.4.0/js/ace.js'

// --- import system ---
import Vue from 'vue'
import VueResource from 'vue-resource'
import axios from 'axios'
import Element from 'element-ui'
import App from '@/App'
import router from '@/router'
import store from '@/vuex/store'

const EventBus = new Vue();

// --- system setting ---
Vue.use(Element);
Vue.use(VueResource);
Vue.prototype.$bus = EventBus;
Vue.prototype.$axios = axios;

/*
router.beforeEach((to, from, next) => {
	if (!to.meta.nonauth) {
		const authUser = JSON.parse(window.localStorage.getItem('authUser'))
		if (!(authUser && authUser.access_token)) {
			next({path: '/login'})
			return
		}
	}
	next()
});
*/

// --- vue instance ---
const app = new Vue({
	el: '#app',
	store,
	router,
 	components: { App },
	template: '<App/>'
});



