<template>
	<div class="breadcrumbs ace-save-state" id="breadcrumbs">
		<ul class="breadcrumb">
			<router-link tag="li" v-for="(v,k) in breadcrumb" :key="k" :to="v.router" v-if="v.router">
				<i class="ace-icon fa fa-home home-icon" v-if="v.name==='home'"></i>
				<a href="#" v-bind:class="{active: v.active}">{{v.name}}</a>
			</router-link>
			<li v-else>{{v.name}}</li>
		</ul>

		<div class="nav-search" id="nav-search">
			<form class="form-search">
				<span class="input-icon">
					<input type="text" placeholder="Search ..." class="nav-search-input" id="nav-search-input" autocomplete="off" />
					<i class="ace-icon fa fa-search nav-search-icon"></i>
				</span>
			</form>
		</div>
	</div>
</template>

<script type="text/javascript">
	import _sidemenu from '@/router/sidemenu.js'

	export default {
		data() {
			return {
				breadcrumb: []
			}
		},
		watch: {
			'$route' (to, from) {
				var p = false
				var c = false

				for (var i = 0; i < _sidemenu.length; i++) {
					if (_sidemenu[i].router && _sidemenu[i].router === to.path) {
						p = false;
						c = _sidemenu[i];
						break;
					}

					if (_sidemenu[i].type === 'tree') {
						p = _sidemenu[i];

						for (var j = 0; j < _sidemenu[i].items.length; j++) {
							if (_sidemenu[i].items[j].router === to.path) {
								c = _sidemenu[i].items[j];
								break;
							}
						}
		
						if (c) break;
					}
				}

				this.breadcrumb = []
				this.breadcrumb.push({ name: 'home', router: '/index'})

				if (c && c.router !== this.breadcrumb[0].router) {
					if (p) this.breadcrumb.push({ name: p.name, router: p.router})
					if (c) this.breadcrumb.push({ name: c.name, router: c.router, active: true})
				}
			}
		},
	}
</script>

<style>
</style>



