<template>
	<div id="app">
		<va-navibar></va-navibar>

		<div class="main-container ace-save-state" id="main-container">
			<va-sidebar></va-sidebar>

			<div class="main-content">
				<div class="main-content-inner">
					<va-pathbar></va-pathbar>
					<div class="page-content">
						<router-view></router-view>
					</div>
				</div>
			</div>

			<va-footerbar></va-footerbar>
		</div>
	</div>
</template>

<script>
	import _navibar from '@/widgets/navibar.vue'
	import _pathbar from '@/widgets/pathbar.vue'
	import _sidebar from '@/widgets/sidebar.vue'
	import _footerbar from '@/widgets/footerbar.vue'
	import _skin from '@/lib/skin.js'

	export default {
		name: 'App',
		components: {
			'va-navibar': _navibar,
			'va-sidebar': _sidebar,
			'va-footerbar': _footerbar,
			'va-pathbar': _pathbar
		},
		mounted: function() {
			try { ace.settings.loadState('main-container') } catch (e) { }
			_skin.load_setting();	// load skin setting
		}
	}
</script>

<style>
	#app {
		margin: 0px;
		padding: 0px;
	}
</style>



