<template>
	<li v-if="item.type==='tree'">
		<router-link :to="item.router" class="dropdown-toggle" v-if="item.router">
			<i class="menu-icon" :class="item.icon"></i>
			<span class="menu-text">{{item.name}}</span>
			<b class="arrow fa fa-angle-down"></b>
		</router-link>
		<a href="#" class="dropdown-toggle" v-else>
			<i class="menu-icon" :class="item.icon"></i>
			<span class="menu-text">{{item.name}}</span>
			<b class="arrow fa fa-angle-down"></b>
		</a>
		<b class="arrow"></b>

		<ul class="submenu">
			<router-link tag="li" v-for="(v,k) in item.items" :key="k" :to="v.router">
			<a href="#">
				<i class="menu-icon" :class="v.icon"></i>
				<span class="menu-text">{{v.name}}</span>
			</a>
			<b class="arrow"></b>
		</router-link>
		</ul>
	</li>
	<router-link tag="li" :to="item.router" v-else>
		<a href="#">
			<i class="menu-icon" :class="item.icon"></i>
			<span class="menu-text">{{item.name}}</span>
		</a>
		<b class="arrow"></b>
	</router-link>
</template>

<script>
	export default {
		name: 'va-sidebar-item',
		props: {
			item: {
				type: Object,
				default () {
					return {}
				}
			}
		}
	}
</script>



